
html,body,#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
  background: #dddddd;
}
.wrapper{
  height:100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.topbar{
  background:#ffffff;
  -webkit-box-shadow:0 1px 3px 0 rgba(0,0,0,0.25);
          box-shadow:0 1px 3px 0 rgba(0,0,0,0.25);
}
main{
  height:100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0;
}
main > .editor {

  margin-left: 16px;
  margin-top: 16px;
  margin-bottom: 19px;
}
main > .preview {

  margin-right: 16px;
  margin-top: 16px;
  margin-bottom: 19px;
}

#topbar{
  border: 1px solid gray;
  max-width:1440px;
  min-width:1024px;
  padding: 16px 16px;
  height:64px;
}
#topbar>#topbar_logo{
  font-family:STHeitiSC-Light;
  font-size:24px;
  color:#000000;
  letter-spacing:0;
  line-height: 32px;
}

#editor {
  width: 34%;
  height: 100%;
  background:#ffffff;
  -webkit-box-shadow:inset 0 1px 3px 0 rgba(0,0,0,0.25);
          box-shadow:inset 0 1px 3px 0 rgba(0,0,0,0.25);
}

#preview{
  width:61.66667%;
  height:100%;
  background:#ffffff;
  -webkit-box-shadow:inset 0 1px 3px 0 rgba(0,0,0,0.25);
          box-shadow:inset 0 1px 3px 0 rgba(0,0,0,0.25);
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*::before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

ol, ul {
  list-style: none; }
